body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: background 0.3s, color 0.3s;
}
:root {
  --primary-color: #6200ee;
  --primary-color-hover: #4500c7;
  --bg-color: #ffffff;
  --text-color: #000000;
}
[data-theme='dark'] {
  --primary-color: #bb86fc;
  --primary-color-hover: #985eff;
  --bg-color: #121212;
  --text-color: #ffffff;
}
.app {
  padding: 2rem;
  max-width: 768px;
  margin: auto;
}