.theme-toggle {
  font-size: 1rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  background: none;
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
  border-radius: 8px;
  transition: all 0.3s ease;
}
.theme-toggle:hover {
  background-color: var(--primary-color);
  color: white;
}